*{
    margin: 0; 
    padding: 0;
    box-sizing: border-box;
  }
 
html, body {
    width: 100%;
    height: 100%;
}


header, footer, nav{
    width: 100%;
    border: 1px solid black;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;     
}

h1, h2{
    width: 100%;
    text-align: center;
}


#mainContainer{
    width: 90%;
    height: 70%;
    margin: 0 auto;
    border: 1px solid black;
}